	module.exports = {"data":{"post":{"timeToRead":5,"canonicalURL":"https://blog.iansinnott.com/integrating-alfred-and-keyboard-maestro/","html":"<p><strong>Update 7/6/2015:</strong> I've now been using this Alfred workflow for over a year and I still use it <em>every single day</em>. It's become vital to my workflow and I highly recommend it to anyone trying to boost their productivity.</p>\n<p>However, other parts of my workflow have evolved quite a bit (such as window management). If you're interested in a full post on the subject <a href=\"https://twitter.com/ian_sinn/\">drop me a line on twitter</a> or leave a comment at the bottom.</p>\n<p>Now back to the original article.</p>\n<h2 id=\"one-step-closer-to-a-mouse-less-workflow\"><a href=\"#one-step-closer-to-a-mouse-less-workflow\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>One step closer to a mouse-less workflow</h2>\n<p>I'm going to show you how to integrate Alfred with the excellent Keyboard Maestro. Why? Because I have a vendetta against my mouse and I'm constantly finding new ways to remove it from my workflow entirely. To this end, the discovery of <a href=\"http://www.keyboardmaestro.com/main/\">Keyboard Maestro</a> by <a href=\"http://www.stairways.com/main/\">Stairways Software</a> was a big win.</p>\n<h3 id=\"keyboard-maestro-km\"><a href=\"#keyboard-maestro-km\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Keyboard Maestro (KM)</h3>\n<p>Super quick introduction: KM makes it easy to assign almost any Mac action to an easy-to-use trigger. It's a great piece of software and is backed by a friendly and accessible developer, Peter Lewis. Seriously, go check it out right now if you haven't yet. It's free to try.</p>\n<h3 id=\"combine--conquer\"><a href=\"#combine--conquer\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Combine &#x26; Conquer</h3>\n<p>So, let's be honest: if you you're already a Mac power user, you probably have far too many hotkeys rattling around in your brain already. This is especially true if your a developer. KM is an excellent piece of software out of box, but adding more hotkeys to the list of things you must remember might not be the ideal solution. But if we could just name our macros in a memorable way and then call them up at will based on that name we could do amazing things.</p>\n<!--more-->\n<p><strong>Enter <a href=\"http://www.alfredapp.com/\">Alfred</a></strong>. By combining the functionality of Alfred with the system control of KM you can make your Mac do pretty much anything, <em>very quickly</em>.</p>\n<h3 id=\"setup\"><a href=\"#setup\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Setup</h3>\n<p>To integrate the two, download the workflow I made for this purpose.</p>\n<p><a class=\"btn btn-download\" href=\"https://github.com/iansinnott/alfred-maestro/raw/master/AlfredMaestro.alfredworkflow\">Download</a></p>\n<p>You can also <a href=\"https://github.com/iansinnott/keyboard-maestro-alfred\">download the source</a> from GitHub if you feel so inclined. Once you have the <code>KeyboardMaestro.alfredworkflow</code> file just double click it to install it in Alfred. Done!</p>\n<h3 id=\"usage\"><a href=\"#usage\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Usage</h3>\n<p>Now, as long as you actually have KM installed you can type <code>km</code> in the Alfred prompt and then the name of a macro. Here's a simple example; I really don't like continually typing out my email, so I made a macro for it:</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/email-macro.png\" alt=\"Email Macro\"></p>\n<p>Notice that I don't even have to assign a trigger to it. This is somewhat of a new feature in KM 6.3, and the reason you must have that version or above. This is a great feature as it leaves room for macros that really deserve their own hotkey and doesn't clutter your mind with unnecessary information. Just type <code>km</code> into Alfred and then start typing the title of the macro:</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/Screen%20Shot%202015-07-06%20at%209.15.08%20PM.png\" alt=\"Email macro example\"></p>\n<p>If I hit enter my email address will be pasted into whatever text I'm editing. This is great for filling forms online. Another one that is use all the time in this same way is date insertion. I always have to think for a second about what the date is. Luckily KM comes to the rescue:</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/Screen%20Shot%202015-07-06%20at%209.18.19%20PM.png\" alt=\"Date macro\"></p>\n<p>Now if I start typing <code>km date:</code> and hit enter, the date will be inserted in the mm/dd/yyyy format:</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/Screen%20Shot%202015-07-06%20at%209.19.00%20PM.png\" alt=\"Date macro example\"></p>\n<p>This example also shows two good examples of the power of KM. First, you get set values, like <code>%NumberDate%</code> that you can pass in. Second, when you want/need to, you can also assign triggers that are fully independent of Alfred and this workflow.</p>\n<p>To start making your own crazy macros, you should hit the KM docs. I'll point you specifically to the <a href=\"http://www.keyboardmaestro.com/documentation/6/calculations.html\">calculations section</a>, where you can dive into some of the more obscure functionality (like the <code>SCREEN</code> function for manipulating application screens. Then from there you can check out the other sections about more general functionality.</p>\n<p><strong>One Last Note:</strong> KM is fully capable of triggering macros by name all on it's own. See?</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/Screen%20Shot%202015-07-06%20at%209.12.29%20PM.png\" alt=\"Keyboard Maestro macro by name\"></p>\n<p>So if you're not an Alfred user, there is a remote possibility that you can get by with just KM. In fact, if you really wanted to you could probably replicate most of Alfred's functionality entirely with KM, but why bother?</p>\n<p>The other reason to use these two programs together is the Active community behind Alfred. This community means you can usually find a workflow to help with whatever your looking to accomplish. Community involvement however is much more limited with KM. This is surprising, considering the built in â€˜Export macro' functionality. KM does have an <a href=\"http://www.keyboardmaestro.com/main/macro-library\">existing macro library</a>, but I'm not sure how many users know about it. That said, this certainly isn't a deal breaker. It's not difficult to make macros in KM, and there <em>is</em> an active community that I think would be happy to contribute macros if there was a well-known, central location at which to share them.</p>\n<h3 id=\"conclusion\"><a href=\"#conclusion\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Conclusion</h3>\n<p>Keyboard Maestro and Alfred make an amazing combination. If you don't have them already, get them now! I myself have <del>just started using it</del> been using it for <strong>well over a year</strong>. I have a ton more functionality planned with this app that I haven't gotten around to implementing (system-wide Vim bindings anyone?). Moreover, as I mentioned before, the developer Peter Lewis is very accessible. When I first showed him how my workflow was inherently limited by KM itself, he added the necessary functionality and shipped it a few days later with KM 6.3! That's amazing.</p>\n<p><strong>One last note:</strong> If you have any issues with this plugin head over to the <a href=\"https://github.com/iansinnott/keyboard-maestro-alfred/issues\">issues section of my GitHub repo</a> and let me know. And if you you find it useful, <a href=\"https://github.com/iansinnott/keyboard-maestro-alfred\">starring the repo</a> on GitHub is a great way to say thanks. Have fun ðŸ˜Ž</p>","frontmatter":{"created":"2013-11-14T08:00:00.000Z","title":"Integrating Alfred With Keyboard Maestro","disqusId":"1965006338"}}},"pathContext":{"id":"/Users/jandrix/dev/sites/blog.iansinnott.com/content/_posts/2013-11-14-integrating-alfred-and-keyboard-maestro.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"JavaScript Everywhere","created":"2013-12-05T08:00:00.000Z"},"slug":"javascript-everywhere"},"prev":{"frontmatter":{"title":"Getting Productive With Fish Shell","created":"2013-11-11T08:00:00.000Z"},"slug":"getting-productive-with-fish-shell"}}}

